:root {
  --color-brown: #5D3A1A;
  --color-beige: #E8D8C3;
  --color-accent: #D35400;
  --color-text: #4A4A4A;
  --color-background: var(--color-beige);
}

body {
  margin: 0;
  background-color: var(--color-background);
  color: var(--color-text);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
